<!-- 
	<TabbedViewer :views="objectOrArray" defaultView="string" />
 -->

<template>
	<div class="tabbed-viewer">
		<nav>
			<span 
				v-for="(value, key) in views" 
				@click="selectedKey = key"
				:class="{ selected: (key === selectedKey) }"
				v-text="key"
			></span>
		</nav>

		<div v-if="selectedViewType === 'htmlFragment'" v-html="views[selectedKey]"></div>
		<keep-alive v-if="selectedViewType === 'vueComponent'">
			<component :is="views[selectedKey]" />
		</keep-alive>

	</div>
</template>

<script>
export default {
	name: 'TabbedViewer',
	props: {
		// Object containing named (key) HTML fragments/Vue instance (value) to show
		views: {
			type: Object,
			required: true
		},
		// Which view to pre-select by default
		defaultView: {
			type: String
		}
	},
	data() {
		return {
			selectedKey: ''
		}
	},
	computed: {
		selectedViewType() {
			let v = this.views[this.selectedKey]
			if (typeof v === 'string') {
				return 'htmlFragment'
			}
			if (typeof v === 'object' || typeof v === 'function') {
				return 'vueComponent'
			}
		}
	},
	components: {
	},
	created() {
		// Select defaultView
		if (this.defaultView) {
			this.selectedKey = this.defaultView
		}
		else {
			// Default selected key to last or first view (or tries to)
			let keys = Object.keys(this.views)
			this.selectedKey = keys[0]
			// this.selectedKey = keys[keys.length-1]
		}
	}
}
</script>

<style>	
.tabbed-viewer {
	margin: 1em 0;
}
.tabbed-viewer nav span {
	display: inline-block;
	padding: 0 0 0.3em 0;
	margin: 0 0.6em 0.6em 0;
	cursor: pointer;
	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none;

	color: #888;
	border-bottom: 1px solid transparent;
}
.tabbed-viewer nav .selected {
	color: black;
	border-bottom: 1px solid black;
}
</style>